.slick-list, .slick-track {
  min-width: 100%;
}

*::-webkit-input-placeholder {
  color: #afafaf;
  opacity: 1;
}

*:-moz-placeholder {
  color: #afafaf;
  opacity: 1;
}

*::-moz-placeholder {
  color: #afafaf;
  opacity: 1;
}

*:-ms-input-placeholder {
  color: #afafaf;
  opacity: 1;
}

.forms input, .forms textarea {
  -webkit-appearance: none;
}

.forms input:ms-expand, .forms textarea:ms-expand {
  display: none;
}

html {
  font-size: 100%;
}

input, a, button, textarea, select {
  outline: none;
}

*, *:after, *:before {
  box-sizing: border-box;
}

[v-cloak] {
  display: none;
}

select {
  -webkit-appearance: none;
  /* for FF */
  -moz-appearance: none;
  text-indent: 0.01px;
  text-overflow: '';
  /* for IE */
  -ms-appearance: none;
  appearance: none !important;
}

select::-ms-expand {
  display: none;
}

address {
  font-weight: 300;
}

body {
  min-width: 320px;
  line-height: 1.2;
  font-family: "Proxima Nova", sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  color: #000;
  overflow-x: hidden;
}

a {
  color: #1e57fa;
  text-decoration: underline;
  font-weight: 400;
}

a[href^="tel"], a[href^="mail"] {
  white-space: nowrap;
}

#content {
  position: relative;
  background: #fff;
  z-index: 40;
  transition: transform .3s ease;
}

.wrapper {
  max-width: 1200px;
  padding: 0 2rem;
  margin: 0 auto;
  box-sizing: content-box;
}

input[type="submit"], button {
  cursor: pointer;
}

img {
  max-width: 100%;
}

li {
  list-style: none;
}

#page-wr {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  z-index: 40;
  transition: transform .5s ease;
}

.footer {
  margin-top: auto;
  z-index: 40;
}

.msgs {
  display: flex;
  align-items: center;
}

.msgs .soc__link {
  background-color: transparent;
}

.input-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.input-group:before {
  content: "";
  display: inline-block;
  height: 2px;
  width: 5px;
  background: #9b9d9f;
  order: 0;
}

.input-group__input {
  width: 42.40838%;
}

.input-group__input:first-child {
  order: -1;
}

.image {
  margin-bottom: 50px;
}

.image--right {
  float: right;
  margin-left: 40px;
}

.image--left {
  float: left;
  margin-right: 40px;
}

.image--figcaption {
  padding: 30px 20px;
  background: #eceeee;
  text-align: center;
  margin-top: 30px;
}

.base-page__inner {
  max-width: 940px;
}

.text-page {
  max-width: 1030px;
  line-height: 1.5;
}

.text-page > *:not(:last-child) {
  margin-bottom: 28px;
}

.text-page h2, .text-page h3, .text-page h4, .text-page h5, .text-page h6, .text-page strong {
  margin-bottom: 15px !important;
}

.text-page h2 {
  font-size: 1.5rem;
  line-height: 2rem;
}

.text-page h3 {
  font-size: 1.5rem;
  line-height: 1.875rem;
}

.text-page h4 {
  font-size: 1.375rem;
  line-height: 1.75rem;
}

.text-page h5 {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.text-page img[align="right"] {
  float: right;
  margin-left: 20px;
}

.text-page img[align="left"] {
  float: left;
  margin-right: 20px;
}

.text-page blockquote {
  border-left: 2px solid #1e57fa;
  padding-left: 21px;
}

.text-page table {
  width: 100%;
  table-layout: fixed;
  color: #000;
}

.text-page table td {
  padding: 15px 23px;
  font-weight: 300;
  vertical-align: middle;
}

.text-page table th {
  padding: 37px 23px;
  font-weight: 300;
  vertical-align: middle;
}

.text-page table[border="1"] td, .text-page table[border="1"] th {
  border: 1px solid #e4e8ea;
}

.text-page ul, .text-page ol {
  max-width: 650px;
  margin-top: 20px !important;
}

.text-page li {
  position: relative;
}

.text-page li:before {
  display: inline-block;
}

.text-page li ul, .text-page li ol {
  margin-top: 0 !important;
  margin-bottom: 20px;
}

.text-page li li {
  padding-left: 20px;
}

.text-page li li:not(:first-child) {
  margin-top: 0;
}

.text-page ul {
  margin-bottom: 30px;
}

.text-page ul li:before {
  content: "";
  background: #000;
  width: 12px;
  height: 1px;
  vertical-align: middle;
  margin: 0 14px 4px 0;
}

.text-page ul ul li:before {
  content: "-";
  height: auto;
  width: auto;
  background: none;
  margin: 0 5px 5px 0;
}

.text-page ol {
  counter-reset: i;
}

.text-page ol li {
  counter-increment: i;
}

.text-page ol li:before {
  content: counter(i) ".";
  margin-right: 4px;
}

.text-page dt, .text-page dd {
  display: inline;
  font-style: italic;
}

.text-page blockquote {
  position: relative;
  display: inline-block;
  padding: 4px 0 7px 17px;
}

.text-page .btn {
  display: inline-block;
  margin-top: 35px;
}

.checkbox__input {
  display: none;
}

.checkbox__input:checked + label:after {
  opacity: 1;
}

.checkbox__label {
  position: relative;
  padding-left: 40px;
  cursor: pointer;
  min-height: 24px;
  display: flex;
  align-items: center;
}

.checkbox__label:before, .checkbox__label:after {
  position: absolute;
  content: "";
}

.checkbox__label:before {
  background: #fff;
  height: 23px;
  width: 23px;
  left: 0;
  top: 0px;
}

.checkbox__label:after {
  transition: opacity .3s ease;
  height: 6px;
  width: 11px;
  left: 6px;
  top: 9px;
  background: url("../img/ico-checbox-check.png") no-repeat;
  opacity: 0;
}

.radio__input {
  display: none;
}

.radio__input:checked + label:after {
  opacity: 1;
}

.radio__label {
  padding-left: 30px;
  min-height: 15px;
  position: relative;
  display: block;
  cursor: pointer;
}

.radio__label:before, .radio__label:after {
  content: "";
  position: absolute;
  display: block;
  border-radius: 50%;
}

.radio__label:before {
  border: 1px solid #e4e8ea;
  height: 15px;
  width: 15px;
  left: 0;
  top: 0;
}

.radio__label:after {
  top: 4px;
  left: 4px;
  background: #1e57fa;
  height: 7px;
  width: 7px;
  transition: opacity .3s ease;
  opacity: 0;
}
